<template>
  <a-table
  :columns="columns"
  :data-source="tableData"
  :pagination="page"
  @change="changePage"
  >
    <div slot="name" class="button" slot-scope="text,record">
        <a-button @click="changeEdit(record)">
          编辑
        </a-button>
        <a-button @click="changeRemove(record)">删除</a-button>
    </div>
  </a-table>
</template>
<script>
const columns = [
  {
    title: 'id',
    dataIndex: 'id',
    key: 'id',
    width: 80,
  },
  {
    title: '标题',
    dataIndex: 'title',
    key: 'title',
    ellipsis: true,
  },
  {
    title: '描述',
    dataIndex: 'desc',
    key: 'desc',
    ellipsis: true,
  },
  {
    title: '类目',
    dataIndex: 'categoryName',
    key: 'category',
    ellipsis: true,
  },
  {
    title: '预售价格',
    dataIndex: 'price',
    key: 'price ',
    ellipsis: true,
  },
  {
    title: '折扣价格',
    dataIndex: 'price_off',
    key: 'price_off',
    ellipsis: true,
  },
  {
    title: '标签',
    dataIndex: 'tags',
    key: 'tags',
    ellipsis: true,
  },
  {
    title: '限制购买数量',
    dataIndex: 'inventory',
    key: 'inventory',
    ellipsis: true,
  },
  {
    title: '上架状态',
    dataIndex: 'status',
    key: 'status',
    ellipsis: true,
    customRender(text, record) {
      return record.status === 1 ? '上架' : '下架';
    },
  },
  {
    title: '操作',
    dataIndex: 'operation',
    key: 'operation',
    scopedSlots: { customRender: 'name' },
  },
];

export default {
  data() {
    return {
    //   data,
      columns,
    };
  },
  props: ['data', 'page', 'edit'],
  computed: {
    tableData() {
      return this.data.map((item) => ({
        ...item,
        key: item.id,
      }));
    },
  },
  methods: {
    changePage(page) {
      console.log(page);
      this.$emit('change', page);
    },
    changeEdit(record) {
      console.log(record);
      this.$emit('edit', record);
    },
    changeRemove(record) {
      this.$emit('remove', record);
    },
  },
};
</script>

<style scoped>
 button{
     display: inline-block;
 }
 .button{
     width: 130px;
 }
</style>
